html,
body {
  position: relative;
  height: 100%;
  margin: 0;
}

.App {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-align: center;
}

.favorites {

}
.favorites__title {
  font-size: 32px;
}
.favorites__panel {
  display: flex;
  margin-top: 10px;
  flex-direction: column;
  justify-content: space-between;
}

.favorites__panel > .quote {
  margin: 20px;
}

.Main {
  padding: 100px 0;
}

.Main__button-generate {
  background-color: transparent;
  border-radius: 10px;
  border:1px solid #666;
  color: #666;
  -webkit-transition: background 1s ease;
  transition: background 1s ease;
  display: flex;
  align-items: center;
  margin: auto;
  padding: 0 10px;
}
.Main__button-generate:hover {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  cursor: pointer;
}

.title {
  font-family: 'Anton', sans-serif;
  letter-spacing: 2px;
  margin: 0;
}
.designer {
  font-family: 'Sacramento';
  position: relative;
  top: 0;
  left: 85px;
  font-size: larger;
}

.quote {
  padding: 20px;
  background-color: antiquewhite;
  margin: 10px auto;
  width: 80%;
  border-radius: 15px;
  min-height: 200px;
}

.quote__cite {
  font-family: 'Handlee', sans-serif;
}

.quote__symbol {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 3.5rem;
  margin-right: 25px;
}

.quote__author {
  font-size: 0.7em;
  letter-spacing: 2px;
  font-family: 'Roboto Mono', sans-serif;
  background-color: black;
  color: #fff;
  margin: 15px auto;
  border-radius: 20px;
  padding: 5px 10px;
}

.hero img {
  width: 100%;
}

.Menu {
  top: 0;
  left: 0;
  padding: 20px;
  background-color: #000;
  color: #999;
}

.Menu-list {
  display: flex;
  justify-content: flex-end;
  list-style: none;
  padding: 0;
  margin: 0;
}
.Menu-item {
  font-size: 12px;
  padding: 10px 10px;
}
.Menu-item > a {
  text-decoration: none;
  color: #999;
  font-family: 'Roboto Mono', sans-serif;
}
.Menu-item > a:hover {
  text-decoration: none;
  color: #fff;
}
.Menu-item .svg {
  vertical-align: text-top;
  font-size: 16px;
}

.footer {
  width: 70%;
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
  background-color: #000;
  color: #fff;
  font-family: 'Roboto Mono';
  font-size: 10px;
  text-align: left;
  border-radius: 10px;
  margin: 20px auto;
  opacity: .7;
}

@media (min-width: 401px) {
  .Main {
    margin-top: 20px;
  }

  .favorites__panel {
    flex-direction: row;
  }

  .quote__author {
    width: 50%;
  }

  .hero img {
    width: 50%;
  }
}

@media (min-width: 901px) {
  .Main {
    padding: 100px 50px;
    border-radius: 50%;
    width: 50%;
    margin: auto;
  }
}

